<template>
  <div class="calendar">
    <div>
      <h2>{{ currentYear }} 年</h2>

      <div class="calendar-month">
        <button @click="PreviousMonth">
          <font-awesome-icon icon="chevron-left" />
        </button>
        <h3>{{ currentMonth + 1 }} 月</h3>
        <button @click="NextMonth">
          <font-awesome-icon icon="chevron-right" />
        </button>
      </div>
    </div>

    <ul class="week">
      <li><span>日</span></li>
      <li><span>一</span></li>
      <li><span>二</span></li>
      <li><span>三</span></li>
      <li><span>四</span></li>
      <li><span>五</span></li>
      <li><span>六</span></li>
    </ul>
    <hr />
    <ul class="days">
      <li
        v-for="(date, index) in DaysOfTheMonth"
        :key="index"
        @click="SelectDate(date.time)"
      >
        <span
          :class="{
            select: date.time === selectDate,
            notCurrentMonth: date.month !== currentMonth,
          }"
        >
          {{ date.day }}
        </span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: {
    inputDate: Number,
  },
  data() {
    return {
      selectDate: 0,
      currentDay: 0,
      currentMonth: 0,
      currentYear: 0,
      lastDayOfMonth: [31, 28, 31, 30, 31, 30, 31, 31, 30, 31, 30, 31],
    }
  },
  computed: {
    DaysOfTheMonth() {
      let days = []
      this.DayOfFeb()
      let count = new Date(this.currentYear, this.currentMonth, 1).getDay()
      let previousMonth = this.currentMonth - 1
      if (this.currentMonth === 0) {
        previousMonth = 11
      }

      for (let i = 1; i <= 42; i++) {
        let date = {
          year: 0,
          month: 0,
          day: 0,
          time: 0,
        }
        if (i <= count) {
          date.month = this.currentMonth - 1
          date.day = this.lastDayOfMonth[previousMonth] - count + i
        } else {
          if (i <= this.lastDayOfMonth[this.currentMonth] + count) {
            date.month = this.currentMonth
            date.day = i - count
          } else {
            date.month = this.currentMonth + 1
            date.day = i - this.lastDayOfMonth[this.currentMonth] - count
          }
        }
        date.year = this.currentYear
        date.time = new Date(date.year, date.month, date.day).getTime()
        days.push(date)
      }
      return days
    },
  },
  methods: {
    DayOfFeb() {
      if (this.currentYear % 400 === 0) {
        this.lastDayOfMonth[1] = 29
      } else if (this.currentYear % 100 !== 0 && this.currentYear % 4 === 0) {
        this.lastDayOfMonth[1] = 29
      } else {
        this.lastDayOfMonth[1] = 28
      }
    },
    PreviousMonth() {
      if (this.currentMonth === 0) {
        this.currentMonth = 11
        this.currentYear -= 1
      } else {
        this.currentMonth -= 1
      }
    },
    NextMonth() {
      if (this.currentMonth === 11) {
        this.currentMonth = 0
        this.currentYear += 1
      } else {
        this.currentMonth += 1
      }
    },
    SelectDate(dateTime) {
      this.selectDate = dateTime
      this.$emit('setDate', dateTime)
      //$emit給父層
    },
  },

  mounted() {
    if (this.inputDate === 0) {
      this.currentDay = new Date().getDate()
      this.currentMonth = new Date().getMonth()
      this.currentYear = new Date().getFullYear()
    } else {
      this.currentDay = new Date(this.inputDate).getDate()
      this.currentMonth = new Date(this.inputDate).getMonth()
      this.currentYear = new Date(this.inputDate).getFullYear()
    }
    this.selectDate = new Date(
      this.currentYear,
      this.currentMonth,
      this.currentDay
    ).getTime()
  },
}
</script>

<style lang="scss">
$main-color: #ff8800;

.calendar {
  .calendar-month {
    width: 50%;
    margin: auto;
    display: flex;
    justify-content: center;

    h3 {
      color: $main-color;
      height: 100%;
      margin: 0 auto;
      text-align: center;
      font-size: 1.8rem;
    }

    button {
      font-size: 1.8rem;
      background-color: transparent;
      padding: 0;
      margin: auto 0;
      border: none;
      outline: none;
    }
  }
  h2 {
    width: 70%;
    margin: 0px auto;
    font-size: 2rem;
    text-align: center;
    color: $main-color;
  }

  hr {
    width: 65%;
    border: 0;
    height: 1px;
    margin: 0 auto;
    background: #333;
    background-image: linear-gradient(to right, #ccc, #333, #ccc);
  }

  ul {
    width: 60%;
    margin: 0px auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;

    li {
      width: calc(100% / 7);
      padding-bottom: calc(100% / 7);
      height: 0;
      margin: 5px auto;
      text-align: center;
      position: relative;

      span {
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        transition: 0.5s;

        display: flex;
        justify-content: center;
        align-items: center;

        cursor: pointer;
      }
    }
  }
  .week {
    color: $main-color;
  }
  .select {
    box-sizing: border-box;
    color: #fff;
    background-color: $main-color;
    border-radius: 999rem;
  }
  .notCurrentMonth {
    color: #bebebe;
  }
}
</style>
