<template>
  <div>
    <Calendar @setDate="SetInputDate" :inputDate="input.date" />

    <form>
      <div class="name">
        <input type="text" placeholder="姓名" v-model.trim="input.name" />
      </div>
      <div class="compute">
        <input
          type="radio"
          name="compute"
          id="amount"
          value="amount"
          v-model="compute"
        />
        <label for="amount">尾數</label>
        <input
          type="radio"
          name="compute"
          id="bag"
          value="bag"
          v-model="compute"
        />
        <label for="bag">袋數</label>
        <input
          type="radio"
          name="compute"
          id="all"
          value="all"
          v-model="compute"
        />
        <label for="all">尾數+袋數 </label>
      </div>
      <div class="compute-field">
        <template v-if="compute !== 'amount' || compute === 'all'">
          <span>
            <input
              type="number"
              placeholder="袋"
              v-model.number="input.bag"
              key="bag"
            />
          </span>
        </template>
        <template v-if="compute === 'amount' || compute === 'all'">
          <span>
            <input
              type="number"
              placeholder="尾"
              v-model.number="input.amount"
              key="amount"
            />
            <input type="checkbox" id="real" v-model="input.real" />
            <label for="real">實</label>
          </span>
        </template>
      </div>
      <input type="button" class="button-add" value="新增" @click="Add()" />
    </form>
  </div>
</template>

<script>
import Calendar from '@/components/CalendarComponent.vue'
export default {
  components: {
    Calendar,
  },
  data() {
    return {
      input: {
        date: 0,
        name: '',
        amount: '',
        bag: '',
        real: false,
        complete: false,
      },
      compute: 'amount',
    }
  },
  methods: {
    SetInputDate(dateTime) {
      this.input.date = dateTime
    },
    Add() {
      switch (this.compute) {
        case 'amount':
          this.input.bag = 0
          break
        case 'bag':
          this.input.amount = 0
          this.input.real = false
          break
        default:
          break
      }

      let id = this.$route.query.id
      let methodName = ''
      let payload = {}
      if (id === '' || id === null || id === undefined) {
        methodName = 'AddData'
      } else {
        methodName = 'UpdateData'
        payload.id = id
      }
      payload.data = this.input
      console.log(payload)
      this.$store.dispatch(methodName, payload).then(() => {
        this.$router.go(-1).then(() => {
          window.scrollTo({
            top: 0,
            behavior: 'smooth',
          })
        })
      })
    },
  },
  created() {
    let id = this.$route.query.id
    if (id !== undefined && id !== '') {
      let data = this.$store.getters.FindData(id)
      console.log(data)

      if (data !== null || data !== undefined || data !== '') {
        this.input = data

        if (data.amount !== 0 && data.bag !== 0) {
          this.compute = 'all'
        } else if (data.amount === 0) {
          this.compute = 'bag'
        } else if (data.bag === 0) {
          this.compute = 'amount'
        }
      }
    } else {
      this.compute = 'amount'
      this.input.date = new Date().getTime()
    }
  },
}
</script>

<style lang="scss">
form {
  width: 100%;
  max-width: 768px;
  position: relative;
  margin: auto;
  text-align: center;

  div {
    width: 100%;
    max-width: 240px;
    margin: auto;
    padding: 20px 0;
  }

  /*移除input number上下箭頭 */
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type='number'],
  input[type='text'],
  input[type='button'] {
    -webkit-appearance: none; /* Safari Chrome */
    -moz-appearance: none; /* FireFox */
    appearance: none;
    background-color: transparent;
    outline: none;
    margin: 0;
  }

  input[type='number'],
  input[type='text'] {
    border: none;
    text-align: right;
    border-bottom: solid 1px #ccc;
  }

  .name {
    text-align: center;

    input {
      width: 100%;
    }
  }

  input[type='radio'] {
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    z-index: -1;

    &:checked + label {
      background-color: #ff8800;
    }
  }

  label {
    display: inline-block;
    box-sizing: border-box;
    width: 70px;
    margin: 0px 5px;
    font-size: 0.8rem;
    line-height: 2;
    border: solid 1px #ccc;
    background-color: #fff;
    transition: 0.5s;
  }

  .compute-field {
    span {
      display: flex;
      margin-bottom: 50px;

      input[type='number'] {
        width: 100%;
        max-width: 220px;
      }
    }
  }

  .button-add {
    border: solid 1px #ccc;
    padding: 5px 10px;
    transition: 0.3s ease-in-out;

    &:hover {
      background-color: #ff8800;
    }
  }
}
</style>
